.vuestic-profile-card {
  width: 12.6875rem;
}
.vuestic-profile-card .photo {
    height: 9.375rem;
    width: 9.375rem;
    border-radius: 50%;
    background-color: #ddd;
    overflow: hidden;
}
.vuestic-profile-card .photo img {
      height: 100%;
      width: 100%;
}
.vuestic-profile-card .name {
    margin-top: 1.325rem;
    margin-bottom: 0;
}
.vuestic-profile-card .location {
    margin-top: 0.8125rem;
}
.vuestic-profile-card .location .icon {
      color: #ddd;
      font-size: 1.2rem;
}
.vuestic-profile-card .location:hover {
      cursor: pointer;
}
.vuestic-profile-card .location:hover .icon {
        color: red;
}
.vuestic-profile-card .social {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 1rem;
    margin-top: 1.25rem;
    width: 100%;
    border-top: solid 0.125rem #eee;
}
.vuestic-profile-card .social .icon {
      color: #adb3b9;
      text-decoration: none;
      font-size: 1.375rem;
      margin-right: .4375rem;
      margin-left: .4375rem;
}
.vuestic-profile-card .social .icon:hover {
        cursor: pointer;
        color: #2f3438;
}



