
.navbar.app-navbar {
  height: 72px;
  padding-left: 24px;
  padding-right: 44px;
  background-color: #333;
}
.navbar.app-navbar .navbar-container {
    width: 100%;
    height: 100%;
    margin: 0;
}
.navbar.app-navbar .avatar-container {
    display: inline-block;
    width: 50px;
    height: 50px;
    background-color: white;
    border-radius: 50%;
    border: 2px solid #ddd;
    overflow: hidden;
}
.navbar.app-navbar .avatar-container img {
      height: 100%;
      width: 100%;
}
.navbar.app-navbar .menu-icon-container {
    padding: 0;
    font-size: 1rem;
}
.navbar.app-navbar .navbar-brand-container {
    position: absolute;
    z-index: 3;
    height: 50%;
    left: 75px;
    top: 0;
}
.navbar.app-navbar .nav-item {
    padding: 0;
    height: 100%;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-toggle {
    padding: 0;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-toggle:after {
      display: none;
}
@media (min-width: 992px) {
.navbar.app-navbar .dropdown.navbar-dropdown.show .dropdown-menu {
      left: auto;
      right: 0;
}
}
.navbar.app-navbar .dropdown.navbar-dropdown.show:after {
    position: absolute;
    bottom: -1.125rem;
    right: calc(50% - 10px);
    width: 0;
    height: 0;
    display: block;
    content: '';
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #333;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-menu {
    margin-top: 1.125rem;
    padding-top: 0;
    width: 100%;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item {
    height: 48px;
    cursor: pointer;
    font-size: 1rem;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item:hover, .navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item:active, .navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item:focus, .navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item.active {
      outline: none;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item.plain-link-item {
      color: #4ae387;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item.plain-link-item:hover, .navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item.plain-link-item:active, .navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item.plain-link-item:focus {
        background: #333;
}
.navbar.app-navbar .dropdown.navbar-dropdown .dropdown-item.plain-link-item .plain-link {
        text-decoration: none;
}
.navbar.app-navbar .notify {
    position: relative;
}
.navbar.app-navbar .notify::after {
      content: '';
      position: absolute;
      right: -6px;
      top: -6px;
      background-color: #4ae387;
      height: 12px;
      width: 12px;
      border-radius: 50%;
}
.navbar.app-navbar .i-nav-notification.notify::after {
    right: -4px;
    top: 0;
}
@media (max-width: 991px) {
.navbar.app-navbar {
      height: 5.625rem;
      padding: 3rem 0.875rem 1.375rem 0.875rem;
}
.navbar.app-navbar .navbar-brand-container {
        width: 4rem;
        top: 0.875rem;
        left: calc(50% - 4rem);
        height: auto;
}
.navbar.app-navbar .navbar-brand-container .navbar-brand {
          height: 50%;
          padding: 0;
          font-size: 50%;
}
.navbar.app-navbar .dropdown.navbar-dropdown.show {
        display: -ms-flexbox;
        display: flex;
}
.navbar.app-navbar .dropdown.navbar-dropdown.show:after {
          bottom: -2rem;
          z-index: 2;
}
.navbar.app-navbar .dropdown.navbar-dropdown.show .dropdown-menu {
          margin-top: 2rem;
          left: auto;
}
.navbar.app-navbar .dropdown.navbar-dropdown.show .dropdown-menu.last {
            right: 0;
}
}



