
.content-wrap {
  margin-left: calc(24px + 225px + 35px);
  padding: 24px 44px 24px 0;
  transition: margin-left 0.3s ease;
}
.content-wrap .pre-loader {
    position: absolute;
    left: calc(50% - 140px/2);
    top: calc(50% - 104px/2);
}
.sidebar-hidden .content-wrap {
    margin-left: 24px;
}
@media (max-width: 991px) {
.content-wrap {
      padding: 2rem 1rem 24px 1rem;
      margin-left: 0;
}
.sidebar-hidden .content-wrap {
        margin-left: 0;
}
}



