
.sidebar {
  height: calc(100vh - 72px - 24px - 24px);
  position: absolute;
  width: 225px;
  top: calc(72px + 24px);
  left: 24px;
  transition: all 0.2s ease;
  opacity: 1;
}
@media (max-width: 991px) {
.sidebar {
      top: 5.8125rem;
      left: 0.1875rem;
      width: calc(100% - 0.375rem);
      z-index: 1000;
}
}
.sidebar .vuestic-scrollbar {
    height: 100%;
}
.sidebar .vuestic-scrollbar .scrollbar-wrapper {
      box-shadow: 0px 8px 14.72px 1.28px rgba(101, 169, 119, 0.3);
}
.sidebar .vuestic-scrollbar .scrollbar-content {
      background: #333;
}
.sidebar-hidden_without-animation .sidebar {
    transition: none;
}
.sidebar-hidden .sidebar {
    top: -150px;
    opacity: 0;
    z-index: -1000;
}
@media (max-width: 991px) {
.sidebar-hidden .sidebar {
        top: 5.8125rem;
        opacity: 0;
        z-index: 1000;
        height: 0;
}
}
.sidebar .sidebar-link {
    position: relative;
    height: 64px;
    padding-left: 25px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    text-decoration: none;
}
.sidebar .sidebar-link.router-link-active, .sidebar .sidebar-link:hover {
      color: #fff;
      background-color: #161616;
}
.sidebar .sidebar-link.router-link-active .sidebar-menu-item-icon, .sidebar .sidebar-link.router-link-active .expand-icon, .sidebar .sidebar-link:hover .sidebar-menu-item-icon, .sidebar .sidebar-link:hover .expand-icon {
        color: #fff;
}
.sidebar .sidebar-link .expand-icon {
      position: absolute;
      right: 16px;
      top: calc(50% - 16px/2);
      font-weight: bold;
      transition: transform 0.3s ease;
}
.sidebar .sidebar-link.expanded .expand-icon {
      transform: rotate(180deg);
}
.sidebar .sidebar-link .sidebar-menu-item-icon {
      font-size: 19px;
      color: #4ae387;
      margin-right: 14px;
}
.sidebar .sidebar-link .sidebar-menu-item-icon.fa-dashboard {
        /* Temp fix */
        position: relative;
        top: -2px;
}
.sidebar .sidebar-submenu-link {
    height: 48px;
}
.sidebar .sidebar-menu, .sidebar .sidebar-submenu {
    list-style: none;
    padding-left: 0;
}
.sidebar .sidebar-menu li, .sidebar .sidebar-submenu li {
      display: block;
      padding-left: 0;
}
.sidebar .sidebar-submenu .sidebar-link {
    padding-left: 74px;
    font-size: 50%;
}
.sidebar .sidebar-menu {
    max-height: 100%;
    margin-bottom: 0;
}
.sidebar .expand-icon {
    color: #4ae387;
}
.sidebar a {
    color: #fff;
    text-decoration: none;
}



